<template>
  <v-container fluid>
    <v-row justify="center">
      <v-col cols="12" md="4">
        <v-card>
          <v-card-text class="text-center">
            <v-btn text color="primary" to="/chpass">Ganti Password</v-btn>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12" md="4">
        <v-card>
          <v-card-text class="text-center">
            <v-btn text color="primary" @click="dlgConfirm=true">Unregister</v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog max-width="300" v-model="dlgConfirm">
      <v-card>
        <v-card-title>Konfirmasi</v-card-title>
        <v-card-text>
          Apakah anda yakin akan berhenti menggunakan layanan ini?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dlgConfirm=false"
            :disabled="loading"
          >Batal</v-btn>
          <v-btn text color="warning" :loading="loading" @click="onOkUnregClick"
          >Ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
"use strict"

import { unregister } from '@/lib/auth'

export default {
  name: "Account",
  data: () => ({
    dlgConfirm: false,
    loading: false,
  }),
  created() {
    this.$store.commit("SET_TOOLBAR", {title: "Akun", backButton: true})
  },
  methods: {
    onOkUnregClick: async function(){
      this.loading = true
      let resp = await unregister()
      this.loading = false
      if (200===resp.code) {
        this.$router.push("/goodbye")
      } else {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error!',
          text: 'Can not unregister!'
        })
      }
    }
  }
}
</script>
